  margin-right: 40px;
  color: #777;
  font-weight: bold;
  font-size: 1.6em;
}
.Home_transactions__12piv button {
  position: absolute;
  top: 0;
  right:0;
  background: #ddd;
  color: #777;
  border: none;
  padding: 12px 8px;
  text-align: center;
  line-height: 0;
  font-size: 0.9em;
  cursor: pointer;
}
.Login_login-form__3o1m0 {
  max-width: 360px;
